.bloc-page {
    width: 80%;
    padding: 0 10%;
}

.colonnes {
    display: flex;
    justify-content: space-between;
}

.colonne {
    width: 49.2%;
}

/* $hauteur-photo: 526px; */
$hauteur-photo: 556px;

.rangee {
    height: $hauteur-photo;
    margin-bottom: 20px;
    /* a {
        z-index: 98;
    } */
    .img-medium {
        height: 100%;
        width: 100%;
        object-fit: cover;
        z-index: -1;
        display: block;
    }  

    > div {
        display: inline;
        &:hover .img-hover {
            opacity: 1;
        }
        .img-hover {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            width: 100%;
            height: 100%;
            /* z-index: 98; */
            margin-top: - $hauteur-photo;
            opacity: 0;
            position: relative;
    
            background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.92) 100%);
            .btn-plein-ecran {
                width: 35px;
                height: 35px;
                align-self: flex-end;
                padding-top: 2%;
                padding-right: 2%;
                cursor: pointer;
            }
            a {
                width: 46px;
                height: 46px;
                margin: auto;
                .btn-oeil {
                    width: 100%;
                    height: auto;
                }
            }
            .img-infos {
                
                display: flex;
                justify-content: space-between;
                color: white;
                padding-left: 3%;
                padding-right: 3%;
                padding-bottom: 2%;
                /* line-height: 21px; */
                p {
                    &:first-child {
                        font-family: $police-secondaire;
                        font-weight: 500;
                        font-size: 14px;
                        text-transform: uppercase;
                    }
                    &:last-child {
                        font-family: $police-principale;
                        font-size: 14px;
                        letter-spacing: 0.1em;
                        text-transform: uppercase;
                    }
                }
            }
        }
    }
    
    
}